body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left, rgb(32, 8, 58) 0%, rgba(0,128,128,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



/* #### Generated By: http://www.cufonfonts.com #### */

@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url(/robofriends/static/media/SEGA.12a3db8b.woff) format('woff');
    }

h1 {
    font-family: 'SEGA LOGO FONT';
    font-weight: 200;
    text-shadow: 10px 1px 30px purple;
    color: rgb(255, 0, 221);
    /* animation: pulse 10s linear infinite; */
}    

h1:hover {
    font-family: 'SEGA LOGO FONT';
    text-shadow: 5px 10px 50px rgb(128, 0, 255);
    color: rgb(255, 0, 251);
}   


h2 {
    text-shadow: 3px 2px 3px rgb(0, 0, 0);
    color: rgb(255, 255, 255);
    /* animation: pulse 10s linear infinite; */
}  

